import React, { useState } from 'react';
import GridContentPackage from '../../components/GridContentPackage';
import GridProfiles from '../../components/GridProfiles';
import GridSignatures from '../../components/GridSignatures';
import PaginationLogin from '../../components/PaginationLogin';
import { Container, Title } from './styles';

const SignatureAndProfilePage = function () {
    const titles = [
        ['Voc possu mais de uma assinatura, qual deseja acessar?', ''],
        ['Agora escolha o perfil no qual deseja assistir.', 'Utilize o app SKY para criar outros perfis.'],
        ['Quais contedos voc deseja que sejam exibidos?', 'Voc pode trocar essa opo na tela de seleo perfil.']
    ];
    const [currentStep, setCurrentStep] = useState(0);
    const handlerChangeStep = step => {
        setCurrentStep(step);
    }
    return (
        <Container>
            <Title>
                <span>
                    Bem-vindo ao SKY Play 
                </span>
                <h1>
                    {titles[currentStep][0]}
                </h1>
                <span>
                    {titles[currentStep][1]}
                </span>
            </Title>
            {currentStep === 0 ?
                <GridSignatures changeStep={handlerChangeStep} />
                : currentStep === 1 ?
                    <GridProfiles changeStep={handlerChangeStep} />
                    : 
                    <GridContentPackage />
            }
            <PaginationLogin currentStep={currentStep} />
        </Container >
    )
}

export default SignatureAndProfilePage;