import React, { useEffect, useState } from 'react';
import { Events } from '../../util/Events';
import { KeyCodes } from '../../util/Utils';
import { Container, Card } from './styles';

import iconOnlyMine from '../../assets/images/my-subscription-mine.png';
import iconAllContent from '../../assets/images/my-subscription-all-content.png';

const GridContentPackage = function () {

    const dataCards = [
        {
            title: "Apenas meu pacote",
            icon: iconOnlyMine,
            text: "Apenas os contedos que fazem parte do seu pacote assinado e podem ser assistidos sero exibidos na sees do SKY Play."
        },
        {
            title: "Todos os contedos",
            icon: iconAllContent,
            text: "Sero exibidos contedos do seu pacote, de outros maiores que o seu e tambm de contedos disponves para alugar."
        }
    ];

    const [selectedOption, setSelectedOption] = useState(0);

    const handlerKeyRight = () => { 
        setSelectedOption(1);
    }
    const handlerKeyLeft = () => { 
        setSelectedOption(0);
    }
    const handlerKeyEnter = () => {
        
     }

    const handlerKey = (evt) => {
        if (evt.keyCode === KeyCodes.KEY_RIGHT) {
            handlerKeyRight();
        }
        if (evt.keyCode === KeyCodes.KEY_LEFT) {
            handlerKeyLeft();
        }
        if (evt.keyCode === KeyCodes.ENTER) {
            handlerKeyEnter();
        }
    }

    useEffect(() => {
        document.addEventListener(Events.keydown, handlerKey, false);
        return () => {
            document.removeEventListener(Events.keydown, handlerKey, false);
        }
    });

    const renderCards = function () {
        return dataCards.map((cardData, index) => {
            const { icon, title, text } = cardData;
            return (
                <Card key={index} isSelected={selectedOption === index} icon={icon}>
                    <div className="image" />
                    <h2>
                        {title}
                    </h2>
                    <div className="text">
                        {text}
                    </div>
                </Card>
            )
        })
    }

    return (
        <Container>
            {renderCards()}
        </Container>
    );

}


export default GridContentPackage;