import styled from "styled-components";
import { COLORS } from "../../styles";
import { getPercent } from "../../util/Utils";

const CardSignatureSizes = {
    heightRelatedSizes: {
        width: 70,
        height: 22,
        margins: 3
    }
}

const { width: cardWidth, height: cardHeight, margins: cardMargin } = CardSignatureSizes.heightRelatedSizes;
const { innerWidth: windowWidth, innerHeight: windowHeight } = window;
const marginDisplay = (windowWidth / 2) - (getPercent(cardMargin / 2, windowHeight)) - getPercent(cardWidth, windowHeight);

const { overlayDarkest: overlayColor, bgBox: bgBoxColor, overlayLightest0,  overlayLightest10, textPrimary: DSWhite, textSecondary } = COLORS;


const Container = styled.div``;

const DisplayCards = styled.div`
    display: flex;
    flex-direction: column;
    height: 60vh;
    transform: ${props => "translate(-" + props.translateX + "px, 0)"};
    transition: all .2s;
    flex-wrap: wrap;
    margin: 6vh 0 0 ${marginDisplay}px;
    position: relative;
`;

const Cursor = styled.div`
    width: ${cardWidth}vh;
    height: ${cardHeight}vh;
    left: ${props => props.position.left}px;
    top: ${props => props.position.top}px;
    border-radius: 1.3vh;
    z-index: 1;
    transition: all .2s;
    position: absolute;
    border: solid .5vh ${DSWhite};
    background-image: linear-gradient(252deg, ${`${overlayLightest10}, ${overlayLightest0}`} );
    `;


const Card = styled.div`
    color:${textSecondary};
    font-size: 2.5vh;
    width: ${cardWidth}vh;
    height: ${cardHeight}vh;
    border: 1.1vh solid ${props => props.isSelected ? overlayColor : bgBoxColor};
    border-radius: 1vh;
    background-color: ${COLORS.bgBox};
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    margin: 0 ${cardMargin}vh ${cardMargin}vh 0;
    div {
        text-align: center;
        span {
            font-size: 3vh;
            display: block;
            color: ${DSWhite};
            margin-bottom: 1vh
        }
    }
`;

export { Container, DisplayCards, Card, Cursor, CardSignatureSizes };