import React, { useEffect, useState } from 'react';
import { tempDataSignatures } from '../../util/tempDataSignaturesAndProfiles';
import { Container, DisplayCards, Card, Cursor, CardSignatureSizes } from './styles';
import { Events } from '../../util/Events';
import { getPercent, KeyCodes } from '../../util/Utils';


const GridSignatures = function (props) {

    const data = tempDataSignatures;
    const [currentPage, setCurrentPage] = useState(0);
    const [currentPosition, setCurrentPosition] = useState([0, 0]);

    const qtyPLines = [
        Math.ceil(data.length / 2),
        Math.trunc(data.length / 2),
    ]

    useEffect(() => {
        document.addEventListener(Events.keydown, handlerKey, false);
        return () => {
            document.removeEventListener(Events.keydown, handlerKey, false);
        }
    });

    const handlerKeyRight = () => {
        let position = currentPosition, current = currentPosition[0];
        position[0]++;
        position[0] = position[0] >= qtyPLines[position[1]] ? qtyPLines[position[1]] - 1 : position[0];
        if (position[0] % 2 === 0 && position[0] !== current) {
            setCurrentPage(currentPage + 1);
        }
        setCurrentPosition([...position])

    }
    const handlerKeyLeft = () => {

        let position = currentPosition;
        position[0] = position[0] > 0 ? position[0] - 1 : 0;
        if (position[0] % 2 === 1) {
            setCurrentPage(currentPage - 1);
        }
        setCurrentPosition([...position])
    }
    const handlerKeyDown = () => {
        let position = currentPosition;
        position[1] = 1;
        position[0] = position[0] >= qtyPLines[1] ? qtyPLines[1] - 1 : position[0];
        setCurrentPosition([...position])
    }
    const handlerKeyUp = () => {
        let position = currentPosition;
        position[1] = 0;
        setCurrentPosition([...position])

    }

    const handlerKeyEnter = () => {
        props.changeStep(1);
    }

    const handlerKey = evt => {
        if (evt.keyCode === KeyCodes.KEY_RIGHT) {
            handlerKeyRight();
        }
        if (evt.keyCode === KeyCodes.KEY_LEFT) {
            handlerKeyLeft();
        }
        if (evt.keyCode === KeyCodes.KEY_DOWN) {
            handlerKeyDown();
        }
        if (evt.keyCode === KeyCodes.KEY_UP) {
            handlerKeyUp();
        }
        if (evt.keyCode === KeyCodes.ENTER) {
            handlerKeyEnter();
        }
    }

    const getSelectedIndex = () => { 
        const column = currentPosition[0] ? currentPosition[0] : 0;
        const row = currentPosition[1] ? currentPosition[1] : 0;

        return column * 2 + row;
    }

    const renderCards = () => {
        return data.map((card, index) => {
            const { name, signatureId, type } = card;
            return (
                <Card key={index} isSelected={index === getSelectedIndex()} >
                    <div>
                        <span className="name">{name}</span>
                        Assinatura: {signatureId}
                    </div>
                    <span className="type">
                        {type}
                    </span>
                </Card>
            );
        });
    }


    let { width: cardWidth, height: cardHeight, margins: cardMargin } = CardSignatureSizes.heightRelatedSizes;
    const { innerHeight: windowHeight } = window;
    cardWidth = getPercent(cardWidth + cardMargin, windowHeight);
    cardHeight = getPercent(cardHeight + cardMargin, windowHeight);

    const getCursosPosition = () => {
        const column = currentPosition[0] ? currentPosition[0] : 0;
        const row = currentPosition[1] ? currentPosition[1] : 0;
        const left = column * cardWidth;
        const top = row * cardHeight;
        return {
            left,
            top
        }
    }

    const displayTranslate = () => {
        return currentPage * cardWidth * 2;
    }

    return (
        <Container>
            <DisplayCards translateX={displayTranslate()}>
                {renderCards()}
                <Cursor position={getCursosPosition()} />
            </DisplayCards>
        </Container>
    )
}

export default GridSignatures;