import React from 'react';
import { Container, Button } from './styles';

const PaginationLogin = function (props) {
    const { currentStep } = props;
    const renderButtons = () => {
        let arrButtons = [];
        for (let i = 0; i < 3; i++) {
            const isDone = i < currentStep;
            arrButtons.push(
                <Button key={i} isCurrent={i === currentStep} isDone={isDone}>
                    {isDone ?
                        <span className="sky_icon sky_icon-line-confirm"></span>
                        : i + 1}
                </Button>
            );
        }
        return arrButtons;
    }
    return (
        <Container>
            {renderButtons()}
        </Container>
    );
}

export default PaginationLogin;