import styled from "styled-components";
import { COLORS } from '../../styles';

const { textPrimary, textDisable, brand } = COLORS;

const Container = styled.div`
    display: flex;
    justify-content: center;
`;

const Button = styled.div`
    color: ${props => props.isCurrent ? textPrimary : textDisable};
    border: ${props => props.isDone ? 'none' : `.3vh solid ${props.isCurrent ? brand : textDisable}`};
    font-size: 3vh;
    text-align: center;
    width: 9vh;
    height: 9vh;
    line-height: 8.5vh;
    margin: 0 3vh;
    border-radius: 50%;
    .sky_icon {
        font-size: 11vh;
        margin: -.2vh -1vh;
    }
    `;

export { Container, Button };