import React, { useEffect, useState } from 'react';
import { Events } from '../../util/Events';
import { tempDataProfiles } from '../../util/tempDataSignaturesAndProfiles';
import { KeyCodes } from '../../util/Utils';
import { CardProfile, Container } from './styles';

const GridProfiles = (props) => {

    const data = tempDataProfiles;

    const [selected, setSelected] = useState(0);

    const renderCards = () => {
        return data.map((profile, index) => {
            return <CardProfile image={profile.image} isSelected={index === selected} key={index} >
                <div></div>
                <span>
                    {profile.name}
                </span>
            </CardProfile>
        });
    }

    const handlerKeyRight = () => {
        if (selected < data.length - 1) {
            setSelected(selected + 1);
        }
    }
    const handlerKeyLeft = () => {
        if (selected > 0) {
            setSelected(selected - 1);
        }
     }
    const handlerKeyEnter = () => {
        props.changeStep(2);
     }

    const handlerKey = (evt) => {
        if (evt.keyCode === KeyCodes.KEY_RIGHT) {
            handlerKeyRight();
        }
        if (evt.keyCode === KeyCodes.KEY_LEFT) {
            handlerKeyLeft();
        }
        if (evt.keyCode === KeyCodes.ENTER) {
            handlerKeyEnter();
        }
    }

    useEffect(() => {
        document.addEventListener(Events.keydown, handlerKey, false);
        return () => {
            document.removeEventListener(Events.keydown, handlerKey, false);
        }
    });

    return (
        <Container>
            {renderCards()}
        </Container>
    )
}

export default GridProfiles;