import styled from "styled-components";
import { COLORS } from "../../styles";

const { textPrimary, textSecondary, overlayDarkest0 } = COLORS;

const Container = styled.div`
    width: 100vw;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
`;

const CardProfile = styled.div`
    color: ${props => props.isSelected ? textPrimary : textSecondary};
    font-size: 3.5vh;
    text-align: center;
    div {
        height: 25vh;
        width: 25vh;
        background-image: url('./images/${props => props.image}');
        background-size: contain;
        margin-bottom: 3vh;
        border-radius: 50%;
        ::before {
            content: ' ';
            display: block;
            border: .5vh solid ${props => props.isSelected ? textPrimary : overlayDarkest0};
            height: 26vh;
            width: 26vh;
            border-radius: 50%;
            margin: -1vh;
            position: absolute;
        }
    }
`;

export { Container, CardProfile };