import styled from "styled-components";
import { COLORS } from "../../styles";

const { textPrimary, overlayDarkest0, textDisable, bgBox } = COLORS;

const Container = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;
`;

const Card = styled.div`
    width: 45vh;
    margin: 0 2vh;
    height: 50vh;
    background-color: ${bgBox};
    text-align: center;
    font-size: 2vh;
    border-radius: 1vh;
    border: .3vh solid ${props => props.isSelected ? textPrimary : overlayDarkest0};
    
    .image {
        height: 15vh;
        margin: 5vh 0;
        background-image: url(${props => props.icon});
        background-position: center;
        background-repeat: no-repeat;
    }

    h2 {
        color: ${props => props.isSelected ? textPrimary : textDisable};
    }

    div {
        color: ${textDisable};
        font-size: 2.5vh;
        padding: 3vh;
    }
`;

export { Container, Card };