import { FILL_MODAL_INFO, TOGGLE_INFO_VISIBILITY } from '../types/shared';

const initialState = {
  moreInfoisVisible: false,
  modalInfoData : {
    title: '',
    description: '',
    actionBtn: [],
    backBtn: {},
    images: [],
  }
};

const shared = (state = initialState, action) => {
    switch (action.type) {
      case FILL_MODAL_INFO:
        return {
          ...state,
          modalInfoData : {
            title: action.payload.title,
            description: action.payload.description,
            actionBtn: action.payload.actionBtn,
            backBtn: action.payload.backBtn,
            images: action.payload.images,
          }
        }
      case TOGGLE_INFO_VISIBILITY: 
        return {
          ...state,
          moreInfoisVisible: action.payload
      }
      default:
        return state
    }
  }
  
  export default shared;